import React, { useRef, useState, useEffect, memo } from 'react';
import { Token } from '@/types';
import { FORMATTERS } from '@/lib/utils';
import { Avatar } from '../atoms/Avatar';

interface TokenRowProps {
  token: Token;
  onClick: (token: Token) => void;
}

// Simple SVG Sparkline Generator
const Sparkline = ({ isPositive }: { isPositive: boolean }) => {
  const color = isPositive ? '#34d399' : '#f43f5e'; // Emerald-400 or Rose-500
  // Random path simulation
  const d = isPositive 
    ? "M0 20 Q10 18 20 15 T40 10 T60 5" 
    : "M0 5 Q10 8 20 12 T40 18 T60 22";
  
  return (
    <svg width="60" height="25" className="opacity-50 group-hover:opacity-100 transition-opacity">
      <path d={d} fill="none" stroke={color} strokeWidth="2" strokeLinecap="round" />
    </svg>
  );
};

export const TokenRow = memo(({ token, onClick }: TokenRowProps) => {
  const prevPriceRef = useRef(token.price);
  const [flash, setFlash] = useState<'green' | 'red' | null>(null);

  useEffect(() => {
    if (token.price > prevPriceRef.current) setFlash('green');
    else if (token.price < prevPriceRef.current) setFlash('red');
    
    prevPriceRef.current = token.price;
    const timer = setTimeout(() => setFlash(null), 600);
    return () => clearTimeout(timer);
  }, [token.price]);

  // Dynamic border glow instead of just background
  const borderClass = flash === 'green' ? 'border-emerald-500/50 shadow-[inset_0_0_20px_rgba(16,185,129,0.1)]' 
                    : flash === 'red' ? 'border-rose-500/50 shadow-[inset_0_0_20px_rgba(244,63,94,0.1)]' 
                    : 'border-zinc-800/30 hover:border-zinc-700 hover:bg-zinc-900/40';

  return (
    <div 
      onClick={() => onClick(token)}
      className={`group relative flex items-center gap-3 p-3 border-b cursor-pointer transition-all duration-300 ${borderClass}`}
    >
      {/* Active Indicator Line */}
      {flash === 'green' && <div className="absolute left-0 top-0 bottom-0 w-0.5 bg-emerald-500 shadow-[0_0_10px_#10b981]" />}
      {flash === 'red' && <div className="absolute left-0 top-0 bottom-0 w-0.5 bg-rose-500 shadow-[0_0_10px_#f43f5e]" />}

      <div className="relative">
        <Avatar src={token.image} size="sm" className="ring-1 ring-zinc-800 group-hover:ring-zinc-600 transition-all" />
        {/* Online/Live Dot */}
        <div className="absolute -bottom-0.5 -right-0.5 w-2.5 h-2.5 bg-black rounded-full flex items-center justify-center">
            <div className="w-1.5 h-1.5 bg-emerald-500 rounded-full animate-pulse" />
        </div>
      </div>
      
      <div className="flex-1 min-w-0 grid grid-cols-12 gap-4 items-center">
        {/* Identity */}
        <div className="col-span-4 flex flex-col justify-center">
          <div className="flex items-center gap-1.5">
            <span className="font-bold text-zinc-100 text-base truncate group-hover:text-white transition-colors">{token.ticker}</span>
            <span className="text-zinc-600 text-xs">/SOL</span>
          </div>
          <span className="text-xs text-zinc-500 font-mono group-hover:text-zinc-400">
            MC: {FORMATTERS.compact(token.mcap)}
          </span>
        </div>

        {/* Sparkline (Visual Flair) */}
        <div className="col-span-3 flex items-center justify-center">
            <Sparkline isPositive={token.change1m >= 0} />
        </div>

        {/* Stats */}
        <div className="col-span-5 flex flex-col items-end justify-center">
          <div className={`flex items-center gap-1 text-base font-mono font-medium tabular-nums ${token.change1m >= 0 ? 'text-emerald-400' : 'text-rose-400'}`}>
            {FORMATTERS.percent(token.change1m)}
          </div>
          <span className="text-xs text-zinc-500 font-mono tabular-nums">
             Vol: {FORMATTERS.compact(token.volume)}
          </span>
        </div>
      </div>
    </div>
  );
}, (prev, next) => {
  return prev.token.price === next.token.price && 
         prev.token.change1m === next.token.change1m &&
         prev.token.mcap === next.token.mcap;
});

TokenRow.displayName = 'TokenRow';